import classes from './Post.module.css'
import React, { useEffect, useState } from 'react'
import { db } from '../../Firebase'
import { useAuth } from '../../Context/AuthContext'
import firebase from 'firebase/app'
import { Modal, Card, Button } from 'react-bootstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { NavLink } from 'react-router-dom'

function Post({post, postId}) {

    const [comments, setComments] = useState([])
    const [comment, setComment] = useState('')
    const {currentUser} = useAuth()
    const [show, setShow] = useState(false);
    const [disabled, setDisabled] = useState(true)
    const [likes, setLikes] = useState(0)

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);


    useEffect(() => {
        let unsubscribe;
        if (postId) {
            unsubscribe = db 
            .collection('posts')
            .doc(postId)
            .collection('comments')
            /* .orderBy('timestamp', 'desc') */
            .onSnapshot((snapshot) => {
                console.log(snapshot.docs.map((doc) => doc.data()))
                setComments(snapshot.docs.map((doc) => doc.data()))
            })
        }
        return unsubscribe;
        
    }, [postId])
    console.log(comments)
    function handleSubmit(e) {
        e.preventDefault()
    }

    function postComment(e) {
        e.preventDefault()

        db.collection('posts').doc(postId).collection('comments').add({
            comment: comment,
            username: currentUser.email,
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
        })
        setComment('')
    }

    function handleDelete() {
        db.collection('posts').doc(postId).delete()
    }

    function onLike() {
        setLikes(likes + 1)
    }
    
    return (
        <div className={classes.post}>
            <div>
                <div className={classes.header}>
                    <div className={classes.name}>
                        <img src={post.avatar} alt='avatar' className={classes.avatar}/>
                        <div className={classes.nickname}>
                            {post.username}
                        </div>
                    </div>
                    <div className={classes.circles} onClick={handleShow}>...</div>
                    <Modal show={show} onHide={handleClose} className={classes.modal}>
                        <button disabled={disabled} className={`${classes.modalBtn} ${classes.red}`}>Пожаловаться</button>
                        <button onClick={handleDelete} className={`${classes.modalBtn} ${classes.red}`}>Удалить пост</button>
                        <button disabled={disabled} className={classes.modalBtn}>Перейти к публикации</button>
                        <button disabled={disabled} className={classes.modalBtn}>Поделиться</button>
                        <button disabled={disabled} className={classes.modalBtn}>Копировать ссылку</button>
                        <button disabled={disabled} className={classes.modalBtn}>Вставить</button>
                        <button onClick={handleClose} className={classes.modalBtn}>Отменить</button>
                    </Modal>
                </div>
                
                <img className={classes.postImg} src={post.imageUrl} alt=''/>
                <div className={classes.action}>
                    <div onClick={onLike}><i className={`fa fa-heart-o ${classes.like}`}/></div>
                    <NavLink to={`/p/${postId}`}>
                        <div><i className={`fa fa-comment-o ${classes.like}`}/></div>
                    </NavLink> 
                    <div><i className={`fa fa-paper-plane-o ${classes.like}`}/></div>
                </div>
                <div className={classes.count}>
                {likes} отметок "Нравится"
                </div>
                <div className={classes.postText}><strong>{post.username}</strong> {post.caption}
                </div>
                <div className={classes.comments}>
                    <div className={classes.allcomments}>Посмотреть все комментарий(5)</div> 
                    {comments.map(comment => {
                        return <p>
                        <b>{comment.username}</b> {comment.comment}</p>
                    })}
                </div>
                <form className={classes.commentsForm} onSubmit={handleSubmit}>
                    <input
                    className={classes.input}
                    placeholder='Добавить комментарий...'
                    value={comment}
                    onChange={(e) => setComment(e.target.value)}/>
                    <button onClick={postComment} disabled={!comment} className={classes.btn}>Опубликовать</button>
                </form>
            </div>

        </div>
    )
}

export default Post
