import React, { useEffect, useState } from 'react'
import { Modal } from 'react-bootstrap';
import { db } from '../../Firebase';
import classes from './AboutPost.module.css'
import firebase from 'firebase/app'
import { useAuth } from '../../Context/AuthContext';

export default function AboutPost(props) {

    const [info, setInfo] = useState('')
    const id = props.match.params.id;
    const [disabled, setDisabled] = useState(true)
    const [show, setShow] = useState(false);
    const [likes, setLikes] = useState(0)
    const [comments, setComments] = useState([])
    const [comment, setComment] = useState('')
    const {currentUser} = useAuth()

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    useEffect(() => {
        db.collection('posts').doc(id).get()
            .then(snapshot => setInfo(snapshot.data()))
    }, [])

    function handleDelete() {
        db.collection('posts').doc(id).delete()
    }

    function onLike() {
        setLikes(likes + 1)
    }

    function postComment(e) {
        e.preventDefault()

        db.collection('posts').doc(id).collection('comments').add({
            comment: comment,
            username: currentUser.email,
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
        })
        setComment('')
    }

    return (
        <div>
            {info ? 
            <div className={classes.aboutPost}>
                <div className={classes.imgWrapper}>
                    <img className={classes.img} src={info.imageUrl} alt=''/>
                </div>
                <div className={classes.infoWrapper}>
                    <div className={classes.headerWrapper}>
                        <div className={classes.header}>
                            <img className={classes.avatar} src={info.avatar} alt='avatar'/>
                            <div className={classes.username}>{info.username}</div>
                            <div className={classes.follow}>Подписки</div>
                        </div>
                        <div className={classes.circles} onClick={handleShow}>...</div>
                        <Modal show={show} onHide={handleClose} className={classes.modal}>
                            <button disabled={disabled} className={`${classes.modalBtn} ${classes.red}`}>Пожаловаться</button>
                            <button onClick={handleDelete} className={`${classes.modalBtn} ${classes.red}`}>Удалить пост</button>
                            <button disabled={disabled} className={classes.modalBtn}>Перейти к публикации</button>
                            <button disabled={disabled} className={classes.modalBtn}>Поделиться</button>
                            <button disabled={disabled} className={classes.modalBtn}>Копировать ссылку</button>
                            <button disabled={disabled} className={classes.modalBtn}>Вставить</button>
                            <button onClick={handleClose} className={classes.modalBtn}>Отменить</button>
                        </Modal>
                    </div>
                    <div className={classes.mainInfo}>
                        <img className={classes.avatarInfo} src={info.avatar} alt='avatar'/>
                        <div className='d-flex'>
                            <div className={classes.usernameInfo}>{info.username}</div>
                            {info.caption}
                        </div>  
                    </div>
                    <div className={classes.actionInfo}>
                        <div className={classes.action}>
                            <div onClick={onLike}><i className={`fa fa-heart-o ${classes.like}`}/></div>
                            <div><i className={`fa fa-comment-o ${classes.like}`}/></div>
                            <div><i className={`fa fa-paper-plane-o ${classes.like}`}/></div>
                        </div>
                        <div className={classes.count}>
                            {likes} отметок "Нравится"
                        </div>
                        <div className={classes.added}>
                            4 часов назад
                        </div>
                    </div>
                    <form className={classes.commentsForm} onSubmit={e => e.preventDefault()}>
                        <input
                        className={classes.input}
                        placeholder='Добавить комментарий...'
                        value={comment}
                        onChange={(e) => setComment(e.target.value)}/>
                    <button onClick={postComment} disabled={!comment} className={classes.btn}>Опубликовать</button>
                </form>
                </div>
            </div>: 'loading'}
        </div>
    )
}
