import React, { useEffect, useState } from 'react'
import Post from '../Post/Post'
import classes from './Content.module.css'
import { db } from '../../Firebase'
import ImageUplolad from '../ImageUpload/ImageUplolad'
import InstagramEmbed from 'react-instagram-embed'; 


export default function Content() {


    const [posts, setPosts] = useState([])

    useEffect(() => {
        db.collection('posts').orderBy('timestamp', 'desc').onSnapshot(snapshot => {
            setPosts(snapshot.docs.map(doc => ({
                id: doc.id,
                post: doc.data()
            })))
        })
    }, [])
     console.log(posts)
    return (
        <div className={classes.posts}>
            <div>
                <ImageUplolad/> 
                {
                    posts.map(({post, id}) => {
                        return <Post key={id} postId={id} post={post}/>
                    })
                }
            </div>
            <InstagramEmbed
            clientAccessToken='483290653077860|b3ff09d76316c0ec418bfb4f616d2544'
            url='https://www.instagram.com/p/CL06raNB9Yc/'
            maxWidth={600}
            hideCaption={false}
            containerTagName='div'
            injectScript
            protocol=''
            onLoading={() => {}}
            onSuccess={() => {}}
            onAfterRender={() => {}}
            onFailure={() => {}}
            className={classes.inst}
            />
        </div>
        
    )
}
