import React, { useState } from 'react'
import { db, storage } from '../../Firebase'
import classes from './ImageUplolad.module.css'
import firebase from 'firebase/app'
import { useAuth } from '../../Context/AuthContext'
import { Modal, Button } from 'react-bootstrap'
import logo from '../../Common/img/logo.png'


export default function ImageUplolad() {

    const [caption, setCaption] = useState('')
    const [progress, setProgress] = useState(0)
    const [image, setImg] = useState(null)
    const {currentUser} = useAuth()

    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    function handleChange(e) {
        if (e.target.files[0]) {
            setImg(e.target.files[0])
        }
    };

    function handleUpload(e) {
        e.preventDefault()
        const uploadTask = storage.ref(`images/${image.name}`).put(image)

        uploadTask.on(
            'state_changed',
            (snapshot) => {
                const progress = Math.round(
                    (snapshot.bytesTransferred / snapshot.totalBytes) * 100
                )
                setProgress(progress)
            },
            (error) => {
                console.log(error)
                alert(error.message)
            },
            () => {
                storage
                    .ref('images')
                    .child(image.name)
                    .getDownloadURL()
                    .then(url => {
                        db.collection('posts').add({
                            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
                            caption: caption,
                            imageUrl: url,
                            username: currentUser.email,
                            avatar: url
                        })

                        setProgress(0)
                        setCaption('')
                        setImg(null)
                    })
            }
        )
    }

    return (
        <div className={classes.uploadPost}>
            <Button variant="secondary" className='w-100 ' onClick={handleShow}>
                Add post
            </Button>

            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                <Modal.Title><img src={logo} alt='instagramClone'/></Modal.Title>
                </Modal.Header>
                    <Modal.Body>
                    <div className={classes.upload}>
                        <progress className={classes.progress} value={progress} max='100'></progress>
                        <textarea className={classes.input} placeholder='Описание:' value={caption} onChange={e => setCaption(e.target.value)}/>
                        <input className={classes.file} type='file' onChange={handleChange}/>
                    </div>
                    </Modal.Body>
                <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                    Close
                </Button>
                <Button variant="primary" onClick={handleUpload}>
                    Upload
                </Button>
                </Modal.Footer>
            </Modal>
        </div>

    )
}
